.App {
  font-family: sans-serif;
  text-align: center;
}
.fill {
  display: flex;
  position: relative;
}
.fill img {
  width: 100%;
}

.block {
  position: absolute;
  border: 1px solid black;
  cursor: pointer;
}

.block-1 {
  width: 5%;
  height: 9%;
  top: 47%;
  left: 3%;
}

.block-2 {
  right: 23%;
  bottom: 15%;
  width: 21%;
  height: 17%;
}

.avatar {
  width: 20%;
  max-width: 24px;
  border-radius: 50%;
  background-color: #00f;
  position: absolute;
}
.avatar:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.avatar-1 {
  left: 45%;
  top: 15%;
}

.avatar-2 {
  border-radius: 50%;
  background-color: blue;
  top: 20%;
  left: 14%;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.size {
  width: 60%;
}
.body {
  min-height: 100vh;
}
